<template>
    <div class="details">
        <div style="overflow-y: scroll;height: 100%;-webkit-overflow-scrolling: touch;">
            <div class="main">
                <van-nav-bar            
                    v-if="share !=1 "
                    @click-left="onClickLeft"
                    @click-right="onClickRight"
                >
                    <van-icon name="share" slot="right" />
                </van-nav-bar>
                <div class="" v-if="data">
                    <div class="header flex justify-space-between"  >
                        <div class="flex">
                            <img :src="data.avatar" class="portrait_img" alt />   
                            <div class="header_center">
                                <p class="header_center_name">{{data.author}}</p>
                                <div class="header_center_label">
                                    <div class="label_grade">
                                    <img class="label_grade_img" :class="{'opacity_0':data.is_carvip != 1 }" v-if='data.is_carvip == 1' :src="data.is_carvip == 1 ? icon_iscarvip : ''" alt />
                                    <span class="label_grade_name">{{data.level}}</span>
                                    </div>
                                    <div class="label_heat">
                                    <img class="label_heat_img" :src='icon_label_d' alt />
                                    <span class="label_grade_name">{{data.extcredits2}}°</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="news flex justify-end" >
                            <div class="usage_news " @click="addZan(data,1)" >
                                <img class="usage_news_img" v-if="data.is_zan == 1" src="../../assets/images/icon_love_selection.png" alt="">
                                <img class="usage_news_img" v-else src="../../assets/images/icon_love_normal.png" alt="">
                                <span>{{data.zan}}</span>
                            </div>
                            <div class="usage_news usage_news_right " @click="addZan(data,2)">
                                <img class="usage_news_img" v-if="data.is_zan == 2"  src="../../assets/images/icon_stepon_selected.png" alt="">
                                <img class="usage_news_img" v-else src="../../assets/images/icon_stepon_normal.png" alt="">
                                <span>{{data.cai}}</span>
                            </div>
                        </div>
                    </div>
                    <div class="content" ref="content">
                        <p class="text"><span class="name">{{data.class_name}}</span>{{data.message}}</p>
                        <div v-if="data.attachment == 2" >
                            <div class="image-box" v-if="data.image_list  ? data.image_list.length : false"> 
                                <div v-if="data.image_list.length == 4">
                                    <div>
                                    <img class="img_content" :src="data.image_list[0]" @click="ImagePreview(data.image_list,0)"  >
                                    <img class="img_content" :src="data.image_list[1]" @click="ImagePreview(data.image_list,1)"  >
                                    </div>
                                    <div>
                                    <img class="img_content" :src="data.image_list[2]" @click="ImagePreview(data.image_list,2)"  >
                                    <img class="img_content" :src="data.image_list[3]" @click="ImagePreview(data.image_list,3)"  >
                                    </div>
                                </div>
                                <div v-else >
                                    <img v-for="(item, index) in data.image_list" :key="index" class="img_content" :src="item" alt="" @click="ImagePreview(data.image_list,index)">
                                </div>
                            </div>
                        </div>
                        <div v-if="data.attachment == 1">
                            <div class="video" v-bind:class="{'video_1':data.video_heng_shu == 1,'video_2':data.video_heng_shu == 0}" v-if="data.attachment == 1" @click="handlePlay('video')" >
                            <video ref="video" :src="data.video" preload="auto" :poster="data.image_list[0]"  ></video>
                            <img class="icon-play" src="../../assets/images/icon_play.png" >
                            </div>
                        </div>
                        <div class="subinfo flex justify-space-between"><p>{{data.address}}</p><span>{{data.dateline|timeformat()}}</span></div>
                    </div>

                    <div class="comment" ref="comment" >
                        <p class="line"></p>
                        <img v-if="this.pageInfo.count == 0" style="width:100%" src="../../assets/images/pic_comment.png"  >
                        <p class="point" v-if="this.pageInfo.count == 0">快给楼主写评论吧</p>
                        <p class="count" v-if="this.pageInfo.count != 0" >全部评论({{this.pageInfo.count}})</p>
                        <van-list
                        v-model="loading"
                        :finished="finished"
                        finished-text=""
                        @load="onLoad"
                        >
                            <ul class="list">
                                <li v-for="(item,key) in commentData" :key="key" >
                                    <div class="head flex justify-space-between"> 
                                        <div class="flex">
                                            <img :src="item.avatar" class="portrait_img" alt />   
                                            <div class="header_center">
                                                <p class="header_center_name">{{item.author}}</p>
                                                <div class="header_center_label">
                                                    <div class="label_grade">
                                                    <img class="label_grade_img" :class="{'opacity_0':data.is_carvip != 1 }" v-if='data.is_carvip == 1' :src="data.is_carvip == 1 ? icon_iscarvip : ''" alt />
                                                    <span class="label_grade_name">{{item.level}}</span>
                                                    </div>
                                                    <div class="label_heat">
                                                    <img class="label_heat_img" :src='icon_label_d' alt />
                                                    <span class="label_grade_name">{{item.extcredits2}}°</span>
                                                    </div>
                                                </div>
                                            </div>
