                                        {{item.message}}
                                    </div>
                                
                                </li>
                            </ul>
                        </van-list>

                    </div>
                </div>
            </div>  
        </div>      
        <div class="input-box"  v-if="share !=1 ">
            <p class="line"></p>
            <div>
                <van-cell-group>
                    <van-field  @focus="animateWidth('company_name','blur')" v-model="postParameter.message" placeholder="我也来说～" :border="false"   @keyup.enter="addPost" >
                        <span slot="button" style="color: #424E77;" @click="addPost" >发布</span>
                    </van-field>
                </van-cell-group>
            </div>
        </div>
