</template>

<script>
import {getFromNom} from '@js/moment'
import { ImagePreview ,Toast} from 'vant';
export default {
  data() {
    return {
        share:this.$route.query.share, // 1: 是从分享页 打开
        loading:false,
        finished:false,
        icon_label_d:require('../../assets/images/icon_label.png'),
        icon_iscarvip:require('../../assets/images/icon_iscarvip.png'),
        data:null,
        commentData:[],
        pageInfo:{
            page:0,
            size:10,
            count:0
        },
        postParameter:{
            token:this.$store.state.userData,
            tid:this.$route.query.tid,
            message:'',
            uppid:'',
            reply_pid:'',
            aid_list:'',
            attachment:0
        }
    }
  },
  created(){
      document.title = ' ';
      if(this.$route.query.share == 1){
         document.title = 'E区新能源车主壹社区';
      }
      this.getSquaresItemDetail();
      this.getSquaresDetailComment();
  },
  methods: {
      onLoad(){
          if(this.pageInfo.page == Math.ceil(this.pageInfo.count/this.pageInfo.size)-1){
              this.finished = true
          }
          if(this.commentData.length){
              this.pageInfo.page++;
              this.getSquaresDetailComment();
          }else {
              
          }
      },
      // 视频播放
        handlePlay(ref){
            let obj = this.$refs[ref];
            obj.play();
            
            obj.webkitRequestFullScreen();
            obj.addEventListener('pause',()=>{
                obj.controls = false;
            })
        },      
      // 输入框获取焦点
      animateWidth(){
          setTimeout(() => {
            if(this.$refs.comment.clientHeight + this.$refs.content.clientHeight > 600){
                window.scrollTo(0,document.body.scrollHeight)
            }else {
                window.scrollTo(0,0)
            }
          },0)         
      },
      // 图片预览
        ImagePreview(v,key){
        ImagePreview({
            images:v,
            startPosition:key
        })
        },
      getSquaresDetailComment(){
          this.api.getSquaresDetailComment({
              token:this.$store.state.userData,
              tid:this.$route.query.tid,
              page_index:this.pageInfo.page,
              page_size:this.pageInfo.size
          }).then(res => {
              this.loading = false;
              if(res.err_code == 0){
                  if(this.pageInfo.page == 0){
                      this.commentData = [];
                  }
                  if(!res.data.post.length){
                      this.finished = true
                  }
                  this.commentData = [...this.commentData,...res.data.post];
                  this.pageInfo.count = res.data.total
                  if(this.pageInfo.page != 0){
                      window.scrollTo(0,document.body.scrollHeight)
                  }
              }
          })
      }, 
        // 点赞或踩
        addZan(item,type){
        if(this.share == 1){
            return null;
        }    
        if(!this.$store.state.userData){
            window.location.href= "teldapp://login"
            return null;
        }
        let tid = item.tid,
            token = this.$store.state.userData;
        Toast.loading({
            message: '加载中...',
            forbidClick: true
        });    
        this.api.addZan({token,tid,type}).then(res => {
            Toast.clear();
            if(res.err_code == 0){
            if(item.is_zan == 1){
                if(type == 1){
                item.zan = parseInt(item.zan)-1
                item.is_zan = 0;
                }else if(type == 2){
                item.is_zan = type;
                item.zan = parseInt(item.zan)-1
                item.cai = parseInt(item.cai)+1
                }
            }else if(item.is_zan == 2){
                if(type == 1){
                    item.is_zan = type;
                    item.zan = parseInt(item.zan)+1
                    item.cai = parseInt(item.cai)-1
                }else if(type == 2){
                    item.is_zan = 0;
                    item.cai = parseInt(item.cai)-1
                }     
            }else {
                if(type == 1){
                item.zan = parseInt(item.zan)+1

                }else if(type == 2){
                item.cai = parseInt(item.cai)+1
                }
                item.is_zan = type;
            }
            if(item.is_zan){

            }
            
           // Toast(res.data.msg)
            }else {
            Toast(res.err_msg)
            }
        })
        },      
      // 回复
      addPost(){
            if(!this.$store.state.userData){
                window.location.href= "teldapp://login"
                return null;
            }
            if(!this.postParameter.message ){
                return Toast('输入不能为空')
            }
            Toast.loading({
                message: '加载中...',
                forbidClick: true
            });
          this.api.addPost(this.postParameter).then( res => {
              Toast.clear();
              if(res.err_code == 0){
                  this.postParameter.message = '';
                  this.pageInfo.page = 0;
                  this.getSquaresItemDetail();
                  this.getSquaresDetailComment();
              }
          })
      },  
      getSquaresItemDetail(){
          this.api.getSquaresItemDetail({
              token:this.$store.state.userData,
              tid:this.$route.query.tid
          }).then( res => {
              if(res.err_code == 0){
                  this.data = res.data.thread
              }
          } )
      },
      onClickLeft(){
        this.$router.go(-1)
      },
      onClickRight(){
          let img = this.data.image_list.length ? this.data.image_list[0] :  this.data.avatar;
          let url = `${this.$store.state.indexUrl+'square/details?share=1'+'&'+this.$store.state.appParams}`,
              json = {url:url,channel:'wxp,wxf,sina,qqp,qqz',title:`【E区】${this.data.message}`,desc:this.data.message,image:img};
          console.log(url);
          window.location.href= `teldapp://share?params=`+JSON.stringify(json)
      }
  },
  filters:{
    timeformat(v){
      if(v){
        return getFromNom(v*1000)
      }else {
        return ''
      }
    }
  }
}

</script>
